import React from 'react';

import { ReactComponent as Logo } from './sfm-logo.svg';

import styled from 'styled-components'
import { Link } from 'react-router-dom';

const StyledBurger = styled.button`
  position: absolute;
  top: 6vh;
  right: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 10;

  &:focus {
    outline: none;
  }

  div {
    width: 2rem;
    height: 0.25rem;
    background: ${({ open }) => open ? '#0D0C1D' : '#1d3557'};
    border-radius: 10px;
    transition: all 0.3s linear;
    position: relative;
    transform-origin: 1px;

    :first-child {
      transform: ${({ open }) => open ? 'rotate(45deg)' : 'rotate(0)'};
    }

    :nth-child(2) {
      opacity: ${({ open }) => open ? '0' : '1'};
      transform: ${({ open }) => open ? 'translateX(20px)' : 'translateX(0)'};
    }

    :nth-child(3) {
      transform: ${({ open }) => open ? 'rotate(-45deg)' : 'rotate(0)'};
    }
  }
`

const StyledList = styled.div`
    width:50%;

`;

const StyledLink = styled(Link)`
    color: #1d3557;
    font-family:"Gotham";
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    justify-content: space-around;
    align-items: center;
    text-decoration: none;
    list-style: none;
    padding: 4vw;
    display: inline-table;

    pointer-events: ${({ open }) => open ? 'auto' : 'none'};
    opacity: ${({ open }) => open ? '1' : '0'};
    transform: ${({ open }) => open ? 'translateX(0px)' : 'translateX(20px)'};
    transition: all 0.3s linear;

    &:hover {
        color: #457b9d;
    }

    @media (max-width: 768px) {
        padding: 10px;
        font-size: 10px;
    }
`;

const Nav = () => {

    const [open, setOpen] = React.useState(false);

    const logostyle = {
        width: "180px",
    };

    return (
        <nav>
            <a href="/"><Logo style={logostyle}/></a>
            <StyledList>
                 <ul>

                         <StyledLink open={open} to="/services">
                             <li>Services</li>
                         </StyledLink>
                         <StyledLink open={open} to="/blog">
                             <li>Blog</li>
                         </StyledLink>
                         <StyledLink open={open} to="/about">
                             <li>About</li>
                         </StyledLink>

                 </ul>
             </StyledList>

             <StyledBurger open={open} onClick={() => setOpen(!open)}>
                 <div />
                 <div />
                 <div />
             </StyledBurger>


        </nav>

    );
}

export default Nav;
